/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #f0f0f0;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Roboto", cursive;
}

h2 {
  font-size: 4rem;
  font-family: "Roboto", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.9) 0%, rgba(59, 72, 98, 0.9) 100%), url(../img/header_bg.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  padding: 5rem 0;
  overflow-x: clip;
}
@media (min-width: 768px) {
  .header {
    padding: 5rem 0 20rem 0;
  }
}
.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 25rem;
}
@media (min-width: 768px) {
  .header__heading {
    margin: 10rem 0 0 0;
    width: 50%;
  }
}
.header__contenido {
  text-align: center;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .header__contenido {
    text-align: left;
  }
}
.header__texto {
  display: none;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .header__texto {
    display: block;
  }
}
.header__imagen {
  display: none;
}
@media (min-width: 768px) {
  .header__imagen {
    display: block;
    position: absolute;
    right: -2rem;
    bottom: -5rem;
    max-width: 40rem;
    border-radius: 2rem;
  }
}

.navegacion__link {
  color: #FFFFFF;
  display: block;
  margin: 1rem 0;
}
@media (min-width: 768px) {
  .navegacion__link {
    display: inline;
    margin: 0 1rem;
  }
}

.propiedades {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .propiedades {
    margin-top: 10rem;
  }
}
.propiedades__titulo {
  font-weight: 400;
}
.propiedades__titulo span {
  font-weight: 700;
}
.propiedades__titulo::after {
  display: block;
  content: "";
  width: 15rem;
  height: 1rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}
@media (min-width: 768px) {
  .propiedades__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}

.propiedad {
  background-color: #FFFFFF;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .propiedad {
    margin-bottom: 0;
  }
}
.propiedad:nth-child(1) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_1.jpg");
}
.propiedad:nth-child(2) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_2.jpg");
}
.propiedad:nth-child(3) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_3.jpg");
}
.propiedad:nth-child(4) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_4.jpg");
}
.propiedad:nth-child(5) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_5.jpg");
}
.propiedad:nth-child(6) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/propiedad_6.jpg");
}
.propiedad__imagen {
  position: relative;
  background-size: auto 150%;
  background-repeat: no-repeat;
  background-position: center;
  width: auto;
  height: 30rem;
}
.propiedad__nombre {
  position: absolute;
  bottom: 1rem;
  left: 2rem;
  color: #FFFFFF;
}
.propiedad__descripcion {
  padding: 2rem;
  font-weight: 700;
}
.propiedad__texto {
  font-size: 1.6rem;
  margin: 0;
}
.propiedad__precio {
  font-size: 2.2rem;
  margin: 0 0 1rem 0;
}
.propiedad__boton {
  width: 100%;
  background-color: #3b4862;
  color: #FFFFFF;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  border: 0;
}

.destacada {
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .destacada__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.destacada__imagen {
  background-image: url(../img/destacada.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 90rem;
  height: 40rem;
}
@media (min-width: 768px) {
  .destacada__imagen {
    height: auto;
    background-size: cover;
  }
}
.destacada__texto {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  background-color: #3b4862;
  padding: 10rem 5rem;
  color: #FFFFFF;
}
.destacada__descripcion p {
  color: #FFFFFF;
}
.destacada__boton button {
  width: 100%;
  background-color: #FFFFFF;
  color: #3b4862;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  border: 0;
}

.testimonios-blog {
  margin-top: 5rem;
}
@media (min-width: 1024px) {
  .testimonios-blog {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}
@media (min-width: 1024px) {
  .testimonios-blog__blog {
    grid-column: 2/4;
  }
}
.testimonios-blog__heading {
  font-weight: 400;
}
.testimonios-blog__heading span {
  font-weight: 700;
  display: block;
}
.testimonios-blog__heading::after {
  display: block;
  content: "";
  width: 15rem;
  height: 1rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}

.testimonios__imagen {
  width: 20rem;
  margin: 0 auto;
}
.testimonios__contenido {
  margin-top: -10rem;
  padding: 15rem 2rem 3rem 2rem;
  background-color: #FFFFFF;
}
.testimonios__nombre {
  text-align: center;
  font-size: 2.8rem;
  font-weight: 700;
}
.testimonios__texto {
  text-align: center;
}
.testimonios__texto::before {
  content: "";
  background-image: url(../img/quote.svg);
  background-repeat: no-repeat;
  background-position: center;
  display: block;
  width: 3rem;
  height: 3rem;
}

.entradas {
  margin-bottom: 2rem;
  background-color: #FFFFFF;
}
.entradas__contenido {
  padding: 1rem;
}
.entradas__nombre {
  margin: 1rem 0;
}
.entradas__meta {
  margin: 0;
  font-size: 1rem;
}
.entradas__descripcion {
  margin-bottom: 2rem;
  font-weight: 700;
  font-size: 1.4rem;
}
.entradas__boton {
  width: 100%;
  background-color: #3b4862;
  color: #FFFFFF;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  border: 0;
}
@media (min-width: 768px) {
  .entradas {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
  .entradas__imagen-contenedor {
    grid-column: 1/3;
  }
  .entradas__imagen {
    height: 100%;
    object-fit: cover;
  }
  .entradas__contenido {
    grid-column: 3/6;
    padding: 2rem;
  }
  .entradas__boton {
    width: 50%;
  }
  .entradas:last-child {
    margin-bottom: 0;
  }
}

.footer {
  background-color: #3b4862;
  text-align: center;
}
.footer__contenido {
  padding: 5rem 0;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .footer__contenido {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}
.footer__nosotros {
  margin: 4rem 0;
}
@media (min-width: 768px) {
  .footer__nosotros {
    margin: 0;
  }
}
.footer__heading {
  font-size: 2.4rem;
}
.footer__descripcion {
  color: #FFFFFF;
  font-size: 1.4rem;
}
.footer__logo {
  margin: 0 auto;
  width: 30rem;
}

.navegacion__heading {
  font-size: 2.4rem;
}
.navegacion__link {
  color: #FFFFFF;
  display: block;
  margin: 1rem 0;
}
@media (min-width: 768px) {
  .navegacion__link {
    margin: 1rem 0;
  }
}

.copyright {
  background-color: #292f3b;
  margin: 0;
  padding: 1rem;
  color: #FFFFFF;
  text-align: center;
}